import React, { useContext, useState, useEffect } from "react";
import {
  Button,
  useDisclosure,
  FormLabel,
  Select,
  Box,
} from "@chakra-ui/react";
import { AddIcon, CloseIcon, CalendarIcon } from "@chakra-ui/icons";
import {
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
} from "@chakra-ui/react";
import { CountriesContext } from "../context/CountriesProvider";
import { CityContext, CitySetterContext } from "../context/CityProvider";

export default function ButtonDrawer() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const firstField = React.useRef();

  let city = useContext(CityContext);
  let setCity = useContext(CitySetterContext);
  const countriesData = useContext(CountriesContext);
  const [selectedCountryId, setSelectedCountryId] = useState("0");
  const [selectedCity, setSelectedCity] = useState("0");
  const [countryCities, setCountryCities] = useState([]);

  const handleChangeCountry = (e) => {
    const countryId = e.target.value;
    const { cities } = countriesData?.find((data) => data.iso2 === countryId);

    setSelectedCountryId(countryId);
    setCountryCities(cities);
  };

  const handleChangeCity = (e) => {
    const city = e.target.value;
    setSelectedCity(city);
  };

  const handleAddCity = () => {
    console.log("nce", city, selectedCity);
    setCity(selectedCity);
    console.log("sonra", city);
    onClose();
  };

  return (
    <>
      <Button leftIcon={<AddIcon />} colorScheme="gray" onClick={onOpen}>
        Sehir ekle
      </Button>
      <Drawer
        isOpen={isOpen}
        placement="right"
        initialFocusRef={firstField}
        onClose={onClose}
        size={"md"}
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerCloseButton />
          <DrawerHeader>Sehir ekle</DrawerHeader>

          <DrawerBody>
            <Box mb="6">
              <FormLabel htmlFor="ulke">lke</FormLabel>
              <Select
                id="ulke"
                value={selectedCountryId}
                onChange={(e) => handleChangeCountry(e)}
              >
                <option value="0">lke Secin</option>
                {countriesData?.map((option) => (
                  <option value={option.iso2}>{option.country}</option>
                ))}
              </Select>
            </Box>
            <Box>
              <FormLabel htmlFor="sehir">Sehir</FormLabel>
              <Select
                id="sehir"
                value={selectedCity}
                onChange={(e) => handleChangeCity(e)}
              >
                <option value="0">Sehir Secin</option>
                {countryCities?.map((city) => (
                  <option value={city}>{city}</option>
                ))}
              </Select>
            </Box>
          </DrawerBody>

          <DrawerFooter>
            <Button
              leftIcon={<CloseIcon />}
              variant="outline"
              mr={3}
              onClick={onClose}
            >
              Cancel
            </Button>
            <Button
              leftIcon={<CalendarIcon />}
              colorScheme="blue"
              onClick={handleAddCity}
            >
              Save
            </Button>
          </DrawerFooter>
        </DrawerContent>
      </Drawer>
    </>
  );
}
