import React, { useContext } from "react";
import {
  SimpleGrid,
  Card,
  CardHeader,
  Heading,
  CardBody,
  Text,
  CardFooter,
  IconButton,
  Flex,
  Box,
  Image
} from "@chakra-ui/react";
import { HamburgerIcon } from "@chakra-ui/icons";
import "./Article.css";
import { arr, CityContext, CitySetterContext, WeatherContext } from "../context/CityProvider";

export default function Article() {
  const city = useContext(CityContext);
  let setCity = useContext(CitySetterContext);
  const weatherData = useContext(WeatherContext);


  return (
    <>
      <div className="articleContainer">
        <SimpleGrid
          spacing={4}
          display="flex"
        >
          {arr.map(weatherData => (
            <div className="card">
            <Card bgColor="" color="white" w={280}>
              <CardHeader>
                <Flex align="center" justify="center">                
                    <Box align="center" justify="center">
                      <Heading size="xs">{new Date().toLocaleString('en-us', {  weekday: 'long' })}</Heading>
                      <Text fontSize="xs">{new Date().toLocaleString('en-us', {  day: 'numeric' })}</Text>
                    </Box>                
                </Flex>
                <IconButton
                    pos="absolute" top="2" right="2"
                    variant="ghost"
                    colorScheme="gray"
                    aria-label="See menu"
                    icon={<HamburgerIcon />}
                  />
              </CardHeader>
              <CardBody align="center" justify="center">
                <Image src={weatherData && `http://openweathermap.org/img/w/${weatherData.weather[0].icon}.png`} />
                <Text mt="4" fontWeight="extrabold">{weatherData && weatherData.name}</Text>
                <Text>{weatherData && weatherData.weather[0].description}</Text>
              </CardBody>
              <CardFooter align="center" justify="center">
                <Flex gap="4" bg="gray" opacity="0.7" borderRadius="6" p="2">
                  <Box p="1" align="center" justify="center">
                    <Text fontSize="xs">CurrentTemp</Text>
                    <Text fontSize="xs" fontWeight="extrabold">
                      {weatherData && weatherData.main.temp.toFixed(0)}C
                    </Text>
                  </Box>
                  <Box p="1" align="center" justify="center">
                    <Text fontSize="xs">Feels Like</Text>
                    <Text fontSize="xs" fontWeight="extrabold">
                    {weatherData && weatherData.main.feels_like.toFixed(0)}C
                    </Text>
                  </Box>
                  <Box p="1" align="center" justify="center">
                    <Text fontSize="xs">Humidity</Text>
                    <Text fontSize="xs" fontWeight="extrabold">
                    {weatherData && weatherData.main.humidity.toFixed(0)}%
                    </Text>
                  </Box>
                </Flex>
              </CardFooter>
            </Card>
          </div>
          ))}
        </SimpleGrid>
      </div>
    </>
  );
}
