import React, { createContext, useState, useEffect } from 'react'

export const CountriesContext = createContext(null);
export const CountriesSetterContext = createContext(null);

export default function CountriesProvider({children}) {
    const [countries, setCountries] = useState();

    useEffect(() => {
        fetch("https://countriesnow.space/api/v0.1/countries")
        .then(res => res.json())
        .then(json => setCountries(json.data))
    }, []);

  return (
    <CountriesContext.Provider value={countries}>
        <CountriesSetterContext.Provider value={setCountries}>
            {children}
        </CountriesSetterContext.Provider>
    </CountriesContext.Provider>
  )
}
