import React, { createContext, useState, useEffect } from 'react'

export const CityContext = createContext(null);
export const CitySetterContext = createContext(null);
export const WeatherContext = createContext(null);
export const arr = [];

export default function CityProvider({children}) {
    const key = "2c006b98e2ee5b157418ce7c4325967d";
    const [city, setCity] = useState("London");
    const [weatherData, setWeatherData] = useState();
    const [selectedAllWeatherDatas, setselectedAllWeatherDatas] = useState([]);

    useEffect(() => {
    fetch(
      `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${key}&units=metric`
    )
      .then((res) => res.json())
      .then((json) => {arr.push(weatherData); setWeatherData(json)});
  },[city]);


  return (
    <CityContext.Provider value={city}>
        <CitySetterContext.Provider value={setCity}>
          <WeatherContext.Provider value={weatherData}>
            {children}
          </WeatherContext.Provider>
        </CitySetterContext.Provider>
    </CityContext.Provider>
  )
}
